<script lang="coffeescript">var addBelow, addDispatcher, cloneListItem, destroyDispatcher, dispatch, dividerDropDispatcher, firstChildIndex, getIntermediateIndex, goToItem, maxIndex, message, minIndex, nestDispatcher, nestNewInto, onDrag, onDragStop, refreshed, style, toggleExpanded, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  debug,
  ButtonIcon,
  FlexRow,
  FlexRowCell,
  IconSvg,
  IconToggleSvg,
  Indent,
  InlineInput
} from "../../Braintapper";

import {
  ListItem,
  ListItemMenu,
  ListDraggableContainer,
  ListDroppableDivider,
  ProgressPopover,
  DueDatePopover
} from "../../Application";

export var list = [];

export var dragState = void 0;

message = "";

style = ":hover {font-weight: bold}";

goToItem = function(childItem) {
  return location.href = `#/lists/${childItem.id}`;
};

updateListItem = function(e) {
  return dispatch("update", e.detail);
};

cloneListItem = function(e) {
  return dispatch("clone", e.detail);
};

destroyDispatcher = function(e) {
  var showDeleteConfirmation;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};

// drag and drop
dividerDropDispatcher = function(e) {
  
  return dispatch("drop", e.detail);
};

/*
navigator.clipboard.readText().then (o)->
  add o
*/
////                  _   ___       _        
////  _ __   ___  ___| |_|_ _|_ __ | |_ ___  
//// | '_ \ / _ \/ __| __|| || '_ \| __/ _ \ 
//// | | | |  __/\__ \ |_ | || | | | || (_) |
//// |_| |_|\___||___/\__|___|_| |_|\__\___/ 
////                                         
nestNewInto = function(e) {
  var newIndex, newStub;
  if (e.detail.list_items.length === 0) {
    newIndex = 0;
  } else {
    newIndex = e.detail.list_items.last().index + 1000;
  }
  newStub = {
    index: newIndex,
    ancestry: e.detail.ancestry.clone().append(e.detail.id),
    name: `New item created at ${Date.create().format('{HH}:{mm}:{ss}')}`
  };
  return addDispatcher({
    detail: newStub
  });
};

nestDispatcher = function(e) {
  return dispatch("nest", e.detail);
};

////            _     _ ____       _               
////   __ _  __| | __| | __ )  ___| | _____      __
////  / _` |/ _` |/ _` |  _ \ / _ \ |/ _ \ \ /\ / /
//// | (_| | (_| | (_| | |_) |  __/ | (_) \ V  V / 
////  \__,_|\__,_|\__,_|____/ \___|_|\___/ \_/\_/  
////                                               
addBelow = function(e) {
  var itemIndex, newIndex, newStub;
  itemIndex = list.list_items.findIndex(function(item) {
    return item.id === e.detail.id;
  });
  if (itemIndex === (list.list_items.length - 1)) {
    newIndex = list.list_items[itemIndex].index + 1000;
  } else {
    newIndex = [list.list_items[itemIndex].index, list.list_items[itemIndex + 1].index].median();
  }
  newStub = {
    index: newIndex,
    ancestry: e.detail.ancestry,
    name: `New item created at ${Date.create().format('{HH}:{mm}:{ss}')}`
  };
  return addDispatcher({
    detail: newStub
  });
};

addDispatcher = function(e) {
  console.log("addDispatcher");
  return dispatch("add", e.detail);
};

onDrag = function(e) {
  return dispatch("dragging", true);
};

onDragStop = function(e) {
  //console.log e
  //console.log "dragStop"
  return dispatch("dragStop", true);
};

toggleExpanded = function(item) {
  // force fix for old data
  if (item.is_expanded.list == null) {
    item.is_expanded["list"] = false;
  }
  if (item.is_expanded.radar == null) {
    item.is_expanded["radar"] = false;
  }
  item.is_expanded.list = !item.is_expanded.list;
  return dispatch("update", {
    id: item.id,
    is_expanded: {
      list: item.is_expanded.list,
      radar: item.is_expanded.radar
    }
  });
};

minIndex = function(list) {
  var min;
  if (Object.has(list, 'list_items')) {
    min = list.list_items.min(function(o) {
      return o.index;
    });
    if (min != null) {
      return min.index - 1000;
    } else {
      return -1000;
    }
  } else {
    return -1000;
  }
};

maxIndex = function(list) {
  var max;
  if (Object.has(list, 'list_items')) {
    max = list.list_items.max(function(o) {
      return o.index;
    });
    if (max != null) {
      return max.index + 1000;
    } else {
      return 1000;
    }
  } else {
    return 1000;
  }
};

getIntermediateIndex = function(currentListIndex) {
  var median;
  if (currentListIndex === (list.list_items.length - 1)) { // last
    return list.list_items.last().index + 1000;
  } else {
    return median = [list.list_items[currentListIndex].index, list.list_items[currentListIndex + 1].index].median();
  }
};

refreshed = function(o) {
  //console.error "--------------------------refreshed listitems"
  //console.log o
  return o.sortBy("index");
};

firstChildIndex = function(list) {
  if (list != null) {
    if (list.length === 0) {
      return -1000;
    } else {
      return list.first().index - 1000;
    }
  } else {
    return -1000;
  }
};

$: firstIndex = minIndex(list);

$: lastIndex = maxIndex(list);

$: levelAncestry = (list.ancestry || []).clone().append(list.id);

$: listItems =  refreshed(list.list_items);
</script>
{#if (list.ancestry)} <!-- first child on a sublist -->
  <ListDroppableDivider item={{ ancestry: (list.ancestry || []).clone().append(list.id), index: minIndex(list), nest: true, pre: true} } indentation={list.ancestry.length + 1} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} firstItem={true} on:add={addDispatcher} index={-1} bind:dragState={dragState}/>
{/if}

{#if listItems}
{#each listItems as listItem, index}


  <ListDraggableContainer item={listItem} index={index} indent={listItem.ancestry.length} on:nest={nestDispatcher} on:update={updateListItem} on:dragging={onDrag} on:dragStop={onDragStop}>
      <div type="list-item" id="{listItem.id}">
        <FlexRow>
          
            <FlexRowCell flex="initial" style="width: {listItem.ancestry.length * 24}px">
              <Indent level={listItem.ancestry.length}/>
            </FlexRowCell>
            <FlexRowCell flex="initial" style="width: 24px">
            
              {#if listItem.list_items}
                {#if listItem.list_items.length > 0}
                  <ButtonIcon on:click={toggleExpanded(listItem)}>
                    <IconToggleSvg value={listItem.is_expanded.list} whenTrue="expanded" whenFalse="collapsed"/>
                  </ButtonIcon>
                {/if}
              {/if}
            </FlexRowCell>

          <FlexRowCell flex="initial"> 
            <ProgressPopover item={listItem} value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="id" style="padding-right: 8px"/>
          </FlexRowCell>

          <FlexRowCell flex style="width: 100px">
              
            <div ellipsis>
              <InlineInput item={listItem} on:update={updateListItem}  let:label>
                {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
                {:else}
                  <!--<span>[{listItem.index}]</span>-->
                  <span ellipsis>{listItem.name || "No Name"}</span>&nbsp;
                  {#if listItem.is_ongoing}
                    <span><IconSvg icon="ongoing" size="12"/></span>
                  {/if}
                {/if}
              </InlineInput>
            </div>

          </FlexRowCell>
          <FlexRowCell flex="initial">
            <FlexRow>
              <!-- due date menu -->
              <FlexRowCell flex="initial">
                <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="id"/>
              </FlexRowCell>
            </FlexRow>
          </FlexRowCell>

        </FlexRow>
      </div>
      <div slot="menu">
        <ListItemMenu item={listItem} on:update={updateListItem} on:clone={cloneListItem} on:destroy={destroyDispatcher} on:add={addBelow} on:nest={nestNewInto}/>
      </div>
    
  </ListDraggableContainer>
  {#if false && (listItem.list_items.length == 0) || (!listItem.is_expanded.list)}
  <!-- first child -->
    
    <ListDroppableDivider item={{ ancestry: listItem.ancestry.clone().append(listItem.id),  index: firstChildIndex(listItem.list_items), nest: true}} indentation={listItem.ancestry.length + 1} on:drop={dividerDropDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
    <div style="height: 4px;"/>
    
  {/if}
  <!-- children -->
  {#if listItem.is_expanded.list }
    <svelte:self list={listItem}  bind:dragState={dragState} on:update={updateListItem} on:drop={dividerDropDispatcher} on:clone={cloneListItem} on:nest={nestDispatcher} on:destroy={destroyDispatcher} on:add={addDispatcher} on:dragging={onDrag} on:dragStop={onDragStop}/>
  {/if}
  <!-- same level trailing -->
  {#if true }
    <ListDroppableDivider item={{ ancestry: levelAncestry, index: getIntermediateIndex(index), nest: true, post: true}} indentation={listItem.ancestry.length} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
    <div style="height: 4px;"/>
  {/if}


{/each}
{/if}

<style lang="sass">[type=list-item] {
  width: 100%;
  max-width: 100%;
}
[type=list-item] [main] {
  width: 100px;
}
[type=list-item]:hover [present] {
  display: flex;
}</style>
