<script lang="coffeescript">
/*

  ____                                      _     _     _   
 |  _ \ ___  ___  ___  _   _ _ __ ___ ___  | |   (_)___| |_ 
 | |_) / _ \/ __|/ _ \| | | | '__/ __/ _ \ | |   | / __| __|
 |  _ <  __/\__ \ (_) | |_| | | | (_|  __/ | |___| \__ \ |_ 
 |_| \_\___||___/\___/ \__,_|_|  \___\___| |_____|_|___/\__|

 */
var deferredSearchResults, destroyListItem, dispatch, getOrSet, getSearchResults, handleSearchMenu, horizon, horizonRange, includeArchived, includeCompleted, jump, keywords, mounted, options, resource, searchTerms, sortBy, sortDescending, style, toggleExpanded, updateHorizon, updateList, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  push,
  pop,
  replace,
  location
} from 'svelte-spa-router';

import {
  debug,
  ButtonIcon,
  IconSvg,
  IconToggleSvg,
  Indent,
  FlexRow,
  FlexRowCell,
  MenuContainer,
  SearchHighlighter
} from "../../Braintapper";

import {
  List,
  ListItem,
  SearchList,
  PageContent,
  PageHeader,
  SearchMenu,
  ListHoverableContainer,
  ProgressPopover,
  DueDatePopover,
  TimeHorizon
} from "../../Application";

debug.on = window.debug;

debug.context = "Radar/Index.svelte";

mounted = false;

onMount(function() {
  return mounted = true;
});

import SearchItems from './_ListItems.svelte';

keywords = "";

getOrSet = function(parameter, override) {
  if (localStorage.getItem(parameter) != null) {
    return localStorage.getItem(parameter);
  } else {
    localStorage.setItem(parameter, override);
    return override;
  }
};

// update a list item
////                  _       _       _     _     _   ___ _                 
////  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
//// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
//// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ | || ||  __/ | | | | |
////  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|___|\__\___|_| |_| |_|
////       |_|                                                              
updateListItem = function(e) {
  var found;
  console.log("Radar.Index update list item");
  console.log(e.detail);
  console.log(resource);
  found = resource.raw_items.find({
    id: e.detail.id
  });
  console.error("found");
  console.debug(found);
  if (found != null) {
    return found.patch(e.detail, getRadarLists);
  } else {
    return console.log("item not found");
  }
};

////                  _       _       _     _     _   
////  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_ 
//// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|
//// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ 
////  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|
////       |_|                                        
updateList = function(e) {
  var found;
  // Update Project Attribute
  found = list.find({
    id: e.detail.id
  });
  debug.table("updateList found", found, true);
  debug.object("e.detail", e.detail);
  if (found != null) {
    Object.merge(found, e.detail);
    return found.patch(e.detail, getRadarLists);
  }
};

// testRouteEvent()

// filters

includeArchived = getOrSet("radar_search_include_archived", true) === "true";

includeCompleted = getOrSet("radar_search_include_completed", true) === "true";

horizon = getOrSet("radar_search_horizon", "14d");

sortBy = getOrSet("radar_search_sort_by", "index");

sortDescending = getOrSet("radar_search_sort_descending", false) === "true";

options = {
  include_completed: getOrSet("radar_search_include_completed", true) === "true",
  include_archived: getOrSet("radar_search_include_archived", false) === "true",
  sort_by: getOrSet("radar_search_sort_by", "index"),
  sort_descending: getOrSet("radar_search_sort_descending", false) === "true",
  horizon: getOrSet("radar_search_horizon", "infinity")
};


// filter functions

updateHorizon = function(e) {
  horizon = e.detail;
  options.horizon = horizon;
  return localStorage.setItem("radar_search_horizon", horizon);
};

handleSearchMenu = function(e) {
  debug.log(`handleSearchMenu::${e.detail}`);
  switch (e.detail) {
    case "toggle_completed":
      includeCompleted = !includeCompleted;
      options.include_completed = includeCompleted;
      return localStorage.setItem("radar_search_include_completed", includeCompleted);
    case "toggle_sort_none":
      sortBy = "index";
      sortDescending = false;
      options.sort_by = sortBy;
      options.sort_descending = sortDescending;
      localStorage.setItem("radar_search_sort_by", sortBy);
      return localStorage.setItem("radar_search_sort_descending", sortDescending);
    case "toggle_sort_name":
      if (sortBy === "name") { // same as before
        sortDescending = !sortDescending;
      } else {
        sortDescending = false;
      }
      sortBy = "name";
      options.sort_by = sortBy;
      options.sort_descending = sortDescending;
      localStorage.setItem("radar_search_sort_by", sortBy);
      return localStorage.setItem("radar_search_sort_descending", sortDescending);
    case "toggle_sort_date":
      if (sortBy === "due_date") { // same as before
        sortDescending = !sortDescending;
      } else {
        sortDescending = false;
      }
      sortBy = "due_date";
      options.sort_by = sortBy;
      options.sort_descending = sortDescending;
      localStorage.setItem("radar_search_sort_by", sortBy);
      return localStorage.setItem("radar_search_sort_descending", sortDescending);
  }
};

jump = function(anchorName) {
  var elem, scrollContainer;
  elem = u(`[name=${anchorName}]`);
  scrollContainer = u("body > main > section").first();
  return scrollContainer.scrollTop = elem.first().offsetTop - 40;
};

list = [];

resource = new SearchList();

getSearchResults = function(updatedKeywords, updatedOptions) {
  var listsLoadedScb;
  console.log("getSearchResults");
  // console.log options
  listsLoadedScb = function(lists) {
    // console.log "got readar lsits"
    lists.forEach(function(list) {
      // console.log list
      return list.treeify();
    });
    return list = lists.sortBy(sortBy, sortDescending);
  };
  
  // console.debug lists
  if (mounted && keywords.trim().length > 0) {
    return resource.select(keywords, updatedOptions, listsLoadedScb, null, null);
  }
};

deferredSearchResults = getSearchResults.debounce(1000);

destroyListItem = function(e) {};

export var list = [];

style = ":hover { font-weight: bold }";

toggleExpanded = function(list) {
  console.log(`toggle Expanded ${list.name}`);
  list.is_expanded_radar = !list.is_expanded_radar;
  return updateList({
    detail: list
  });
};

horizonRange = function(range, includeCompletedFlag, ongoingOnlyFlag, sortByFlag, sortDescendingFlag) {
  var itemsLabel, ongoingSuffix, rangeLabel, sortDescendingLabel, sortLabel;
  itemsLabel = "in progress items";
  if (includeCompletedFlag) {
    itemsLabel = "all items";
  }
  ongoingSuffix = " as well as items flagged as ongoing";
  if (ongoingOnlyFlag) {
    itemsLabel = "only items flagged as ongoing";
    ongoingSuffix = "";
  }
  sortDescendingLabel = "";
  if (sortDescendingFlag) {
    sortDescendingLabel = " (descending)";
  }
  sortLabel = "";
  switch (sortByFlag) {
    case "name":
      sortLabel = `, sorted by name${sortDescendingLabel}`;
      break;
    case "due_date":
      sortLabel = `, sorted by date${sortDescendingLabel}`;
  }
  rangeLabel = "";
  switch (range) {
    case "0d":
      rangeLabel = "for today";
      break;
    case "1d":
      rangeLabel = "for yesterday, today and tomorrow";
      break;
    case "1d":
    case "7d":
    case "14d":
      rangeLabel = ` between ${Date.create().addDays(parseInt(horizon) * -1, true).medium()} and ${Date.create().addDays(parseInt(horizon), true).medium()}`;
      break;
    // Math.abs(Date.create().beginningOfDay().daysSince(Date.create().beginningOfDay())) 
    case "1m":
    case "3m":
    case "6m":
    case "12m":
      rangeLabel = ` between ${Date.create().addMonths(parseInt(horizon) * -1, false).medium()} and ${Date.create().addMonths(parseInt(horizon), false).medium()}`;
      break;
    case "1y":
      rangeLabel = ` between ${Date.create().addYears(parseInt(horizon) * -1, false).medium()} and ${Date.create().addYears(parseInt(horizon), false).medium()}`;
      break;
    default:
      rangeLabel = "for all time";
  }
  return `Showing ${itemsLabel}${ongoingSuffix} items ${rangeLabel}${sortLabel}.`;
};

searchTerms = "";

$: deferredSearchResults(keywords, options);

$: sortedList = list;
</script>


<svelte:head>
  <title>Radar</title>
</svelte:head>

<PageContent>
  <PageHeader>
      <FlexRow>
        <FlexRowCell flex>
          <h1 title>Search</h1>
        </FlexRowCell>
        <FlexRowCell style="padding-top: 16px">
          <SearchMenu options={options} on:select={handleSearchMenu}/>
        </FlexRowCell>
      </FlexRow>  
  </PageHeader>

  <TimeHorizon  value={horizon} on:update={updateHorizon}/>
  

  <div horizon-label>
    <p>{ horizonRange(horizon, includeCompleted, null, sortBy, sortDescending) }</p>

  </div>
  <section search-container>
  
    
      <input type="text" placeholder="Type search terms in here" bind:value={keywords} id="searchbox"/>
    
  </section>
  <section list>

    {#if sortedList.length > 0}
      {#each sortedList as list }

        <section radar>
          <!-- list -->
          <ListHoverableContainer>
              <div slot="pre">
                <div collapsible>
                  <ButtonIcon on:click={toggleExpanded(list)} style="height: 32px; width: 32px; left: -6px; position: relative">
                    <IconToggleSvg value={list.is_expanded.radar} whenTrue="expanded" whenFalse="collapsed" size="32"/>
                  </ButtonIcon>
                </div>              
              </div>
              <div>
                <FlexRow classes="radar-list" style="line-height: 36px">


                  <FlexRowCell flex="initial" style="padding-right: 8px; padding-top: 3px">
                    <ProgressPopover item={list} value={list.progress} on:update={updateList} on:open on:close/>
                  </FlexRowCell>

                  <FlexRowCell flex>
                    <div ellipsis>
                      <h3 ellipsis><a href="#/lists/{list.id}"><SearchHighlighter text={list.name || "No Name"} keywords={keywords.split(" ")}/></a></h3>
                    </div>
                      

                  </FlexRowCell>

                  <FlexRowCell flex="initial">
                    <div layout="row">
                      <div flex="initial" present={list.due_date != null} visible-on-hover>
                        <DueDatePopover item={list} value={list.due_date} on:update={updateList} on:open on:close/>
                      </div>
                    </div>
                  </FlexRowCell>

                  
              </FlexRow>
            </div>
            <div slot="menu"><IconSvg/></div>
          </ListHoverableContainer>
          <hr/>

          <!-- list items -->

          {#if list.list_items}
            {#if list.list_items.length > 0}
              {#if list.is_expanded.radar == true}
              <SearchItems {list} {keywords} on:destroy={destroyListItem} on:update={updateListItem} listId={list._id}/>
              {/if}
            {:else}
              While the list has a date attached, no list items in this list with date assigned.
            {/if}
          {:else}
            While the list has a date attached, no list items in this list with date assigned.!!!
          {/if}


         

        </section>

      {/each}
    {:else}
      <section>
        <p>No search matches.</p>
      </section>
    {/if}


  </section>
</PageContent>
<style lang="sass">[search-container] {
  text-align: center;
  margin-bottom: 32px;
}

#searchbox {
  border: 1px solid var(--black);
  border-radius: 4px;
  width: 90%;
}

hr {
  margin-bottom: 8px;
}

section[radar] {
  margin-bottom: 36px;
}

ul[slot=nav] {
  margin-top: 100px;
  padding-left: 32px;
}
ul[slot=nav] a {
  cursor: pointer;
}
ul[slot=nav] li {
  padding: 4px 0px;
}

header {
  margin-bottom: var(--spacing-m);
}
header h1 {
  font-size: 36px;
  color: var(--darkest-blue);
}

[horizon-label] {
  margin-top: 18px;
  margin-bottom: 36px;
  width: 100%;
}
[horizon-label] p {
  margin: 0px 24px;
  color: var(--darker-gray);
  text-align: center;
}

[collapsible], [menu], [progress] {
  width: 32px;
}

.radar-list {
  max-height: 42px;
  border-radius: 3px;
  width: calc(100% + 96px);
  max-width: calc(100% + 96px);
  margin-left: -64px;
  margin-bottom: 8px;
  box-sizing: border-box;
  padding: 4px 0px;
}
.radar-list [menu] {
  text-align: center;
}
.radar-list .slot {
  width: 100%;
  max-width: 100%;
}
.radar-list [visible-on-hover] svg path {
  fill: transparent;
}
.radar-list:hover {
  background: var(--list-row-highlight);
}
.radar-list:hover [visible-on-hover] svg path {
  fill: var(--gray);
}
.radar-list:hover [visible-on-hover]:hover svg path {
  fill: var(--blue);
}</style>
