<script lang="coffeescript">var dispatch, mounted, popover;

import {
  createEventDispatcher,
  onMount
} from "svelte";

dispatch = createEventDispatcher();

import TippyCss from "./Css.svelte";

import tippy from 'tippy.js';

mounted = false;

onMount(function() {
  return mounted = true;
});

export var template = void 0;

export var theme = "context-menu";

// allow binding from parent
export var tippyObject = void 0;

export var tippyComponent = void 0;

export var tippyContent = void 0;

export var style = void 0;

export var classes = void 0;

export var id = void 0;

export var options = {
  theme: theme,
  trigger: "click",
  placement: "right-start",
  interactive: true,
  offset: [0, 0],
  popperOptions: {
    modifiers: [
      {
        name: "flip",
        options: {
          fallbackPlacements: ["top",
      "right",
      "left"]
        }
      }
    ]
  },
  onCreate: function(instance) {
    tippyComponent = new tippyContent({
      target: instance.popper.querySelector('.tippy-content')
    });
    dispatch("create");
    tippyComponent.$on("tippy-event", function(e) {
      // console.log 'tippy-event'1
      // console.log e.detail
      tippyObject.hide();
      return dispatch(e.detail.event, e.detail.value);
    });
  },
  onHide: function(instance) {
    // tippyObject.destroy()
    return dispatch("hide");
  },
  onShow: function(instance) {
    return dispatch("show");
  }
};

popover = function(element, content) {
  tippyContent = content;
  if (!(typeof content === 'function')) {
    options.content = content;
  }
  if (Object.keys(options).length === 0) {
    tippyObject = tippy(element);
  } else {
    tippyObject = tippy(element, options);
  }
  element.addEventListener("click", function(event) {
    tippyObject.setProps({
      getReferenceClientRect: function() {
        return {
          height: 0,
          top: event.clientY,
          bottom: event.clientY,
          left: event.clientX,
          right: event.clientX
        };
      }
    });
    return tippyObject.show();
  });
  return;
  return {
    update: function(params) {
      console.error("update");
      dispatch("update");
    },
    //if tippyComponent
    //  tippyComponent.$set { dateValue: newValue }
    destroy: function() {
      // tippyObject.destroy()
      console.error("tippy destroy");
      return dispatch("destroy");
    }
  };
};
</script>

{#if mounted}
<div use:popover={template} {style} class={classes} {id}>
  <slot/> 
</div>
{/if}

<style global lang="sass">:global(.tippy-box[data-theme=context-menu]) {
  background-color: var(--white);
  border: 1px solid var(--gray);
  border-radius: 0;
  width: 240px;
}
:global(.tippy-box) :global(.tippy-content) {
  padding: 0px;
}</style>
