<script lang="coffeescript">var destroyDispatcher, dispatch, goToList, toggleExpanded, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  debug,
  ButtonIcon,
  IconSvg,
  IconToggleSvg,
  Indent,
  FlexRow,
  FlexRowCell,
  InlineInput,
  SearchHighlighter
} from "../../Braintapper";

import {
  ProgressPopover,
  DueDatePopover,
  ListItemMenu,
  ListHoverableContainer
} from "../../Application";

export var keywords = "";

export var list = void 0;

export var listId = void 0;

goToList = function() {
  return location.href = `#/lists/${listId}`;
};

toggleExpanded = function(item) {
  console.log("toggle Expanded");
  item.is_expanded.radar = !item.is_expanded.radar;
  return dispatch("update", {
    id: item.id,
    is_expanded: {
      radar: item.is_expanded.radar,
      list: item.is_expanded.list
    }
  });
};

updateListItem = function(e) {
  console.log("Radar._ListItems menu");
  console.log(e.detail);
  if (listId != null) {
    console.log(`top event listid: ${listId}`);
    e.detail["list_id"] = listId;
  }
  return dispatch("update", e.detail);
};

destroyDispatcher = function(e) {
  var showDeleteConfirmation;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};
</script>


{#each list.list_items as listItem, index}
  
  <ListHoverableContainer>
    <div type="radar-item">
      <FlexRow classes="radar-item">

        
        <FlexRowCell flex="initial" style="width: {(listItem.ancestry || []).length * 32}px">
          <Indent level={(listItem.ancestry || []).length} width={32}/>
        </FlexRowCell>
            
        <FlexRowCell classes="collapsible">
          <div collapsible>
            {#if (listItem.list_items || []).length > 0}
              <ButtonIcon on:click={toggleExpanded(listItem)}>
                <IconToggleSvg value={listItem.is_expanded.radar} whenTrue="expanded" whenFalse="collapsed"/>
              </ButtonIcon>
              
            {:else}
              <IconSvg/>
            {/if}
          </div>
        </FlexRowCell>    
        <FlexRowCell flex>
          <FlexRow>
            <FlexRowCell flex="initial">
              <div progress>
                <ProgressPopover item={listItem} value={listItem.progress} on:update={updateListItem} on:open on:close/>
              </div>
            </FlexRowCell>
            <FlexRowCell flex style="width: 100px">
              <div ellipsis>
                {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
                {:else}<span ellipsis><SearchHighlighter text={listItem.name || "No Name"} keywords={keywords.split(" ")}/></span>&nbsp;
                {/if}            
                {#if listItem.is_ongoing}
                  &nbsp;
                  <span><IconSvg icon="ongoing" size="12"/></span>
                {/if}
              
              </div>
            </FlexRowCell>
            <FlexRowCell flex="initial">
              <div visible-on-hover>
                <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close/>
              </div>
            </FlexRowCell>
          
          </FlexRow>
        </FlexRowCell>

      </FlexRow>
    </div>
    <div slot="menu">
      <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar"/>
    </div>
  </ListHoverableContainer>

    <!-- children -->
    {#if (listItem.list_items || []).length > 0}
      {#if listItem.is_expanded.radar }
        <svelte:self list={listItem} {listId} on:update={updateListItem} on:destroy={destroyDispatcher} {keywords}/>
      {/if}
    {/if}
  
{/each}


<style lang="sass">[type=radar-item] {
  background: transparent;
  box-sizing: border-box;
  padding: 4px 0px;
}
[type=radar-item] [item-interior] {
  width: calc(100%);
  max-width: calc(100%);
}
[type=radar-item] [collapsible], [type=radar-item] [menu], [type=radar-item] [progress] {
  width: 32px;
}
[type=radar-item] [menu] {
  text-align: center;
}
[type=radar-item] .slot {
  width: 100%;
  max-width: 100%;
}
[type=radar-item] [show-on-hover] svg path {
  fill: transparent;
}
[type=radar-item] [show-on-hover][is-showing=true] svg path {
  fill: var(--blue) !important;
}
[type=radar-item] [collapsible] {
  line-height: 24px;
}
[type=radar-item] [collapsible] svg, [type=radar-item] [collapsible] svg.menu {
  display: inline;
}
[type=radar-item]:hover [show-on-hover] svg path {
  fill: var(--gray);
}
[type=radar-item]:hover [show-on-hover]:hover svg path {
  fill: var(--blue);
}
[type=radar-item]:hover [handle] svg, [type=radar-item]:hover [handle] svg.menu, [type=radar-item]:hover [collapsible] svg, [type=radar-item]:hover [collapsible] svg.menu, [type=radar-item]:hover [draggable-item-menu] svg, [type=radar-item]:hover [draggable-item-menu] svg.menu {
  display: inline;
}
[type=radar-item]:hover [show-on-hover] svg path {
  fill: var(--gray);
}
[type=radar-item]:hover [show-on-hover]:hover svg path {
  fill: var(--blue);
}
[type=radar-item]:hover [collapsible] button[icon] {
  padding: 0px !important;
}
[type=radar-item]:hover [collapsible] svg path.fill {
  fill: var(--light-gray);
}
[type=radar-item]:hover [collapsible]:hover svg path {
  fill: var(--blue);
}</style>
