<script lang="coffeescript">
/*

Drop Cases

1. Drop to Reorder

2. Drop to Nest and Reorder

If target ancestry is the same as origin, then it''s a reorder 

If target ancestry is different from origin, then it''s a nested move

*/
var add, click, clickCount, clickReset, collapse, delayedReset, dispatch, double, drop, hover, isHover, isInvalid, isOver, leave, menuSelect, over, single, tippyComponent, tippyObject, toggleExpanded, unhover;

import {
  createEventDispatcher
} from "svelte";

import {
  slide
} from "svelte/transition";

import {
  sineIn,
  sineOut
} from 'svelte/easing';

dispatch = createEventDispatcher();

import {
  debug,
  Indent,
  FlexRow,
  FlexRowCell,
  IconSvg,
  TippyContextMenu
} from "../../../Braintapper";

import Menu from "./_DividerMenu.svelte";

export var index = void 0;

export var indentation = 0;

export var item = {
  is_divider: true
};

export var listId = void 0;

export var firstItem = false;

export var delay = 250; // click

export var dragState = false;

isOver = false;

isHover = false;

isInvalid = false;

export var isExpanded = false;

over = function(e) {
  e.preventDefault();
  e.stopPropagation();
  isOver = true;
  return isInvalid = false;
};

// console.error "OVER"

//if isInvalid
//  return e.preventDefault()
//console.log "return is invalid: #{!isInvalid}"
// return !isInvalid
hover = function(e) {
  e.preventDefault();
  e.stopPropagation();
  if (!isOver) {
    return isHover = true;
  }
};

// console.error "HOVER"
unhover = function(e) {
  e.preventDefault();
  e.stopPropagation();
  if (!isOver) {
    // console.log "UNHOVER"
    isHover = false;
    return collapse();
  }
};

leave = function(e) {
  var draggable;
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

// console.error "LEAVE"
// isHover = false
add = function(name) {
  var toIndex;
  console.log(`add: index: ${item.index}`);
  toIndex = item.index;
  isHover = false;
  isExpanded = false;
  console.error("divider add");
  console.debug({
    ancestry: item.ancestry,
    index: toIndex
  });
  if (name != null) {
    return dispatch("add", {
      ancestry: item.ancestry,
      index: toIndex,
      name: name
    });
  } else {
    return dispatch("add", {
      ancestry: item.ancestry,
      index: toIndex
    });
  }
};

toggleExpanded = function() {
  return isExpanded = !isExpanded;
};

collapse = function(e) {
  // console.log "collapse"
  if (e != null) {
    e.stopPropagation();
  }
  return isExpanded = false;
};

// Click tracking
clickCount = 0;

clickReset = function() {
  switch (clickCount) {
    case 1:
      return single();
    case 2:
      return double();
    default:
      return clickCount = 0;
  }
};

// }
delayedReset = clickReset.debounce(delay);

single = function() {
  return clickCount = 0;
};

// toggleExpanded()
// collapse.delay(5000)

//dispatch "clicked", id
double = function() {
  return clickCount = 0;
};

// add()
//dispatch "doubleclicked", id
click = function(e) {
  e.stopPropagation;
  clickCount++;
  return delayedReset();
};

drop = function(e) {
  var ancestry, dragItem, draggable, dropItem, dropped, eventType;
  isOver = false;
  isInvalid = false;
  draggable = false;
  eventType = "drop";
  ancestry = item.ancestry.clone();
  // ancestry.pop()

  //console.log "NestableDivider.drop item"
  //console.debug item
  dropItem = item;
  
  // { ancestry: ancestry, index: item.index }
  console.log("dropItem");
  console.log(dropItem);
  console.log(e.detail);
  //console.log "NestableDivider.svelte"
  e.preventDefault();
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  //console.log item

  //console.log "--------------------------------"
  if (!firstItem) {
    if ((item.list_items || []).length > 0) {
      //console.log "------------------------------ DIVIDER DROP"
      //console.log "dragitem"
      //console.debug dragItem
      //console.log "dropitem"
      //console.log item

      //console.log "(item.list_items || []).length #{(item.list_items || []).length} :: nest"
      eventType = "drop";
      item["to_index"] = 0;
    } else {
      console.log(`DROP ITEM ${item.name} index: ${item.index}`);
      item["to_index"] = item.index + 1;
    }
    // console.log "evntType: #{eventType}"
    dropped = true;
    if (!Object.has(item, "is_divider")) {
      item["is_divider"] = true; // handle first item
    }
  } else {
    //console.log "DroppableDivider - drop"
    //console.log item
    console.error("FRIST ITEM");
    item["to_index"] = 0;
    console.log(dragItem);
    console.log(`eventType ${eventType}`);
    dropped = true;
  }
  if (dropped) {
    return dispatch("drop", {
      divider: true,
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: dropItem, // item
      dropIndex: index + 1,
      dropListId: listId
    });
  }
};

tippyComponent = void 0;

tippyObject = void 0;

menuSelect = function(e) {
  switch (e.detail) {
    case "add":
      return add();
    case "add-clipboard":
      console.log("added from clipboards");
      return navigator.clipboard.readText().then(function(o) {
        return add(o);
      });
  }
};
</script>

<div
  divider
  class:over={isOver}
  class:hover={isHover}
  is-dragging={dragState}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
  on:mouseenter={hover}
  on:mouseleave={unhover}
  class:expanded={isExpanded ? "expanded" : false}
  index={item.index}
>
  <FlexRow>
  <FlexRowCell flex="initial">
    <Indent level={indentation} offset={24} flex="initial"/>
  </FlexRowCell>
  <FlexRowCell flex>
    <TippyContextMenu template={Menu} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} on:select={menuSelect}>
    <div 
      class:expanded={isExpanded ? "expanded" : false}
      class:over={isOver}
      
      class="indented"
    >
   <!--  {item.index} :: ancestry {JSON.stringify(item.ancestry)}  -->
    
    {#if isExpanded}
      <div class="add-divider"
        in:slide="{{delay: 150, duration: 300, easing: sineIn }}"
        class:invalid={isInvalid}
         
         layout="row"
      >


      </div>
    {/if}
    
      <div class="droptarget"
        class:invalid={isInvalid}>
          &nbsp;

          &nbsp;
      </div>
      </div>
    </TippyContextMenu>
  </FlexRowCell>

   
  </FlexRow>


</div>


<style lang="sass">div[divider] {
  height: 8px;
  border-radius: 4px;
}
div[divider] .indented {
  border-radius: 4px;
  background: transparent;
}
div[divider]:hover .indented {
  background: var(--list-row-highlight);
}
div[divider].expanded {
  height: 40px;
}
div[divider] .expanded {
  padding: 8px 0px;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  height: 40px;
}
div[divider] .expanded .indented {
  background: transparent;
}
div[divider] .droptarget {
  height: 8px;
  color: transparent;
}
div[divider] .indented.over .droptarget {
  padding: 0px 8px;
  color: var(--light-gray) !important;
  background: transparent;
  border-radius: 4px;
}
div[divider] .add-divider {
  text-align: left;
  border-radius: 3px;
  border: 0px;
  padding: 0px 8px;
  top: -6px;
  vertical-align: middle;
}
div[divider] .add-divider button {
  margin-right: 8px;
}
div[divider] .add-divider [icon] {
  padding-right: 8px;
  color: var(--green);
}
div[divider] .add-divider [icon] svg {
  fill: var(--green);
}
div[divider] .add-divider button[cancel] {
  margin-left: 18px;
  color: var(--red);
}
div[divider] .add-divider button[cancel] svg {
  fill: var(--red);
}
div[divider][is-dragging=true] .indented {
  background: #f1f1f1;
}
div[divider][is-dragging=true] .droptarget {
  padding: 0px 8px;
  color: var(--light-gray) !important;
  background: transparent;
  border-radius: 4px;
}
div[divider][is-dragging=true] .droptarget svg path {
  fill: var(--light-gray);
}
div[divider][is-dragging=true].over .indented {
  background: var(--gray);
  color: white;
}
div[divider].invalid .placeholder {
  background: var(--red) !important;
}
div[divider] div[spacer] {
  width: 24px;
}</style>
