var cleanListItems, cleanLists, compact, deleteIndices, destroy, dispatch, edition, findBadDocTypes, fixListsWithMismatchedIds, forceFix, forceSync, importData, migrate, notificationPermssionRequest, rawDump, replicationDb, replicationEnabled, replicationRequiresAuthentication, restore, saveAs, toggleSync, updateDb, updatePassword, updateUser;

