  return replicationEnabled = localStorage.getItem("radar_sync");
};

importData = function() {
  var fileArray, reader;
  console.error("importData");
  console.debug(files);
  if (files != null) {
    console.log("file info");
    fileArray = Array.from(files);
    console.debug(fileArray.first());
    reader = new FileReader();
    reader.onloadend = function(response) {
      var jsonToData;
      console.log("READ FILE");
      console.log(response);
      jsonFile = response.target.result;
      jsonToData = JSON.parse(jsonFile).map(function(o) {
        delete o._rev;
        return o;
      });
      jsonToData.forEach(function(item) {
        //item.id = uuid()
        return item["_id"] = item.id;
      });
      // item.name = "#{item.name} imported at #{Date.create()}"
      return db.bulkDocs(jsonToData).then(function(result) {
        console.error("imported");
        return console.debug(result);
      });
    };
    return reader.readAsText(fileArray.first());
  }
};

