saveAs = function() {
  // full export
  return db.allDocs({
    include_docs: true,
    attachments: false
  }).then(function(result) {
    var blob, groupedListItems, intermediate, jsonString, listItems, lists, orphanedItems, others, output;
    intermediate = result.rows.map(function(row) {
      return row.doc;
    });
    console.log(`Intermediate items: ${intermediate.length}`);
    output = [];
    orphanedItems = [];
    lists = intermediate.filter(function(item) {
      delete item._rev;
      delete item._id;
      return item.doctype === "List";
    });
    lists.sortBy("name");
    console.log(`LISTS ${lists.length}`);
    console.log(lists);
    listItems = intermediate.filter(function(item) {
      return item.doctype === "ListItem";
    });
    others = intermediate.filter(function(item) {
      var test;
      test = false;
      if (item.doctype != null) {
        if (!["List", "ListItem"].includes(item.doctype)) {
          test = true;
        }
      } else {
        if (item.id != null) {
          test = true;
        }
      }
      return test;
    });
    orphanedItems.append(others);
    console.log(`----------- process list items ${listItems.length}`);
    console.log(`others: ${others.length}`);
    console.log(listItems);
    console.log(others);
    groupedListItems = listItems.groupBy("list_id");
    // console.debug groupedListItems
    lists.forEach(function(list, index) {
      //console.log "LIST"
      //console.log list
      output.append(list);
      if (index < 2) {
        console.debug(output);
      }
      if (groupedListItems[list.id] != null) {
        return output.append(groupedListItems[list.id].sortBy(function(listItem) {
          return listItem.ancestry.join(" ");
        }));
      } else {
        return orphanedItems.append(groupedListItems[list.id]);
      }
    });
    output.append(orphanedItems.map(function(o) {
      o["list_id"] = "orphan";
      return o;
    }));
    //console.table output.map (o)->
    //  return {
    //    id: o.id
    //    doctype: o.doctype
    //    name: o.name
    //    list_id: o.list_id 
    //  }
    jsonString = JSON.stringify(output, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    return FileSaver.saveAs(blob, `braintapper-radar-archive-${Date.create().toTimestampId()}.json`);
  });
};

forceSync = function() {
  console.log(remoteDb);
  if (typeof remoteDb !== "undefined" && remoteDb !== null) {
    console.log("syncing to remote db");
    return db.sync(remoteDb, {
      retry: true
    }).on('complete', function() {
      return console.log("complete!");
    }).on('change', function(change) {
      // yo, something changed!
      console.log("sync change");
      return console.log(change);
    }).on('paused', function(info) {
      // replication was paused, usually because of a lost connection
      console.log("sync paused");
      return console.log(info);
    }).on('active', function(info) {
      // replication was resumed
      console.log("sync active");
      return console.log(info);
    }).on('error', function(err) {
      console.log("sync error");
      return console.log(err);
    });
  }
};

