migrate = function() {
  var migrateScb, output, staging;
  console.error("start migration");
  staging = [];
  output = [];
  migrateScb = function(docs) {
    var blob, jsonString;
    docs.forEach(function(doc) {
      return console.log(`doc: ${doc.name}, list_items: ${doc.list_items.length}`);
    });
    
    // doc.migrate(staging)
    console.table(staging);
    staging.forEach(function(item) {
      var newModel;
      if (item.type === "List") {
        newModel = new List(item);
      } else {
        newModel = new ListItem(item);
      }
      return output.append(newModel.pouchify());
    });
    jsonString = JSON.stringify(output, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    // note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
    // use a different handler for electron
    FileSaver.saveAs(blob, `braintapper-radar-migrated-${Date.create().toTimestampId()}--${md5(jsonString)}.json`);
    return db.bulkDocs(output).then(function(result) {
      console.error("imported new lists and list items");
      return console.debug(result);
    });
  };
  return resource.selectAll(migrateScb);
};

findBadDocTypes = function() {
  return db.find({
    selector: {
      doctype: {
        $exists: false
      }
