forceFix = function() {
  return db.find({
    selector: {
      $or: [
        {
          "id": "9a219ca9-410d-4bc6-acd2-52fe86fbc112"
        }
      ]
    }
  }).then(function(response) {
    return console.table(response.docs);
  }).catch(function(err) {
    if (typeof catchCb !== "undefined" && catchCb !== null) {
      return catchCb(err);
    } else {
      console.log("destroy.catch");
      return console.log(err);
    }
  }).finally(function() {
    console.log("destroy finally");
    if (typeof finallyCb !== "undefined" && finallyCb !== null) {
      return finallyCb();
    }
  });
};

fixListsWithMismatchedIds = function() {
  return db.allDocs({
    include_docs: true,
    attachments: false
  }).then(function(result) {
    var mismatched, mismatchedDocs, mismatchedIds;
    mismatched = result.rows.filter(function(o) {
      return (o.doc._id !== o.doc.id) && (!o.doc._id.startsWith("_"));
    });
    mismatchedDocs = mismatched.map(function(o) {
      return o.doc;
    });
    console.table(mismatchedDocs);
    mismatchedIds = mismatchedDocs.map(function(o) {
      return o.id;
    });
    console.debug(mismatchedIds);
    return db.find({
      selector: {
        list_id: {
          $in: mismatchedIds // remove dupes
        }
      }
    }).then(function(secondaryResult) {
      var changes, mismatchedListItems, mismatchedListMap;
      mismatchedListItems = secondaryResult.docs.sortBy("list_id");
      mismatchedListMap = mismatchedListItems.groupBy("list_id");
      console.log(mismatchedListMap);
      changes = [];
      Object.keys(mismatchedListMap).forEach(function(key) {
        var found;
        found = mismatchedDocs.find(function(o) {
          return o.id = key;
        });
        changes.append({
          _id: found._id,
          id: found._id,
          doctype: "List",
          old_id: found.id
        });
        return changes.append(mismatchedListMap[key].map(function(o) {
          return {
            _id: o._id,
            list_id: key
          };
        }));
      });
      console.table(changes);
      console.log(changes.map("_id").unique());
      return db.find({
        selector: {
          _id: {
            $in: changes.map("_id")
          }
        }
      }).then(function(result) {
        console.log("changedocs");
        return console.debug(result.docs.groupBy("doctype"));
      });
    });
  });
};

deleteIndices = function() {
  return db.getIndexes().then(function(indexesResult) {
    return indexesResult.indexes.forEach((index) => {
      if (index.name !== "_all_docs") {
        console.log(index);
        return db.deleteIndex(index);
      }
    });
  });
};

