<script lang="coffeescript">
/*
  events
    nest
    update
    dragging
    dragStop
*/
var dispatch, draggable, draggableEl, drop, end, getWidth, isDragging, isInvalid, isOver, leave, mousedown, mouseup, over, start;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  debug,
  ButtonIcon,
  FlexRow,
  FlexRowCell,
  IconSvg,
  IconToggleSvg,
  Indent
} from "../../../Braintapper";

export var item = void 0;

export var index = void 0;

export var indent = 0;

export var listId = void 0;

draggableEl = void 0;

draggable = false;

isOver = false;

isInvalid = false;

isDragging = false;

start = function(e) {
  item["listId"] = listId;
  item["index"] = index || null;
  e.dataTransfer.setData("text/plain", JSON.stringify(item.safe_model()));
  e.dataTransfer.setData(`listid:${listId}`, JSON.stringify(item.safe_model()));
  isDragging = true;
  return dispatch("dragging", true);
};

end = function(e) { // stop dragging
  //console.error "END"
  //console.log e.type
  isDragging = false;
  //dragStop = ()->
  //   console.log
  return dispatch("dragStop", true);
};


// debouncedFinish = dragStop.debounce(250)
// debouncedFinish()
over = function(e) {
  e.preventDefault();
  return isOver = true;
};

leave = function(e) {
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

drop = function(e) {
  var dragItem, dropped;
  isOver = false;
  isInvalid = false;
  draggable = false;
  e.preventDefault();
  dispatch("dragStop", true);
  console.log("preparse");
  console.log(e.dataTransfer.getData("text/plain"));
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  console.log("postparse");
  dropped = true;
  if (dropped) {
    return dispatch("nest", {
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: item,
      dropIndex: index,
      dropListId: listId
    });
  }
};

mouseup = function() {
  // console.error "MOUSEUP"
  draggable = false;
  isDragging = false;
  return isInvalid = false;
};

mousedown = function() {
  isDragging = true;
  return draggable = true;
};

getWidth = function(el) {
  if (el != null) {
    return el.clientWidth - 48;
  } else {
    return 0;
  }
};
</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}
  bind:this={draggableEl}
>
  <FlexRow>
  
  
    <FlexRowCell flex="initial" >
      <div on:mouseup={mouseup(item)} on:mousedown={mousedown(item)} item-draggable-handle is-dragging={isDragging} visible-on-hover>
        <IconSvg classes="handle" icon="handle"/>
      </div>      
    </FlexRowCell>

    <FlexRowCell flex="true">
      <slot/>
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <div item-draggable-menu  visible-on-hover>
        <slot name="menu"/>
      </div>
    </FlexRowCell>
  </FlexRow>



</div>


<style global lang="sass">:global(:root) {
  --item-draggable-icon-width: 32px;
}

:global([item-draggable]) {
  border-radius: 3px;
  width: calc(100% + calc(2 * var(--item-draggable-icon-width)));
  max-width: calc(100% + calc(2 * var(--item-draggable-icon-width)));
  margin-left: calc(var(--item-draggable-icon-width) * -1);
}
:global([item-draggable]) :global([item-draggable-handle]), :global([item-draggable]) :global([item-draggable-menu]) {
  width: var(--item-draggable-icon-width);
}
:global([item-draggable]) :global([item-draggable-handle]) :global(svg) :global(path), :global([item-draggable]) :global([item-draggable-menu]) :global(svg) :global(path) {
  fill: var(--light-gray);
}
:global([item-draggable]) :global([item-draggable-handle]) {
  width: 24px;
  height: 24px;
}
:global([item-draggable]) :global([item-draggable-handle]:hover) {
  cursor: grab;
}
:global([item-draggable]) :global([item-draggable-handle][is-dragging=true]) {
  cursor: grabbing !important;
}
:global([item-draggable]) :global([item-draggable-menu]) {
  padding-left: 8px;
}
:global([item-draggable]) :global([visible-on-hover]) :global(svg) :global(path) {
  fill: transparent;
}
:global([item-draggable]:hover) {
  background: var(--list-row-highlight);
}
:global([item-draggable]:hover) :global([visible-on-hover]) :global(svg) :global(path) {
  fill: var(--light-gray);
}
:global([item-draggable].over) {
  background: var(--light-blue) !important;
}</style>
